.landing-about {
    background-color: #f8f6f0;
    color: #002337;
    &__inner {
        display: flex;
        flex-wrap: wrap;
        max-width: 1440px;
        margin: 0 auto;
        padding: 182px 73px 100px;
    }
    &__title {
         width: 50%;
         display: inline-block;
         vertical-align: top;
         font-family: 'PF DinDisplay Pro';
         font-size: 24px;
         font-weight: 900;
         font-style: normal;
         font-stretch: normal;
         line-height: 1.17;
         letter-spacing: 1.3px;
         text-transform: uppercase;
    }
    &__desc {
         width: 100%;
         padding-left: 73px;
         font-family: 'PF Din Text Cond Pro';
         font-size: 15px;
         font-weight: normal;
         font-style: normal;
         font-stretch: normal;
         line-height: 1.27;
         letter-spacing: 0.47px;
         column-count: 2;
         column-gap: 72px;
         margin-bottom: 40px;
         span {
             color: #e61841;
         }
    }
    &__vacancy {
         position: relative;
         width: 50%;
         text-align: right;
         font-family: 'PF Din Text Cond Pro';
         font-size: 16px;
         font-weight: normal;
         font-style: normal;
         font-stretch: normal;
         line-height: 1.44;
         letter-spacing: 0.7px;
         color: #e61841;
         text-transform: uppercase;
         padding-right: 30px;
         &:after {
             position: absolute;
             content: '';
             width: 16px;
             height: 16px;
             right: 0;
             top: 4px;
             background: url("/images/desktop/icons/arrow.svg") 50% 50%/contain no-repeat;
             transition: all .2s linear;
         }
         &:hover {
            &:after {
                 right: -5px;
                 top: 5px;
                 width: 14px;
                 height: 14px;
            }
         }
    }
    &__video {
         width: 876px;
         height: 411px;
         border-radius: 3px;
         box-shadow: 0 10px 24px -4px rgba(8, 41, 67, 0.37), 0 6px 4px -5px rgba(0, 0, 0, 0.12);
         background-color: #000000;
         margin: 0 auto 81px;
    }
    &__rehabilitation {
        width: 100%;
        display: flex;
        justify-content: space-between;
    }
    &__block {
        width: 50%;
        img {
            width: 80%;
            max-width: 440px;
            height: auto;
            margin-top: 62px;
        }
        ul {
            height: 100%;
            display: flex;
            flex-direction: column;
            justify-content: space-between;
            font-family: 'PF DinDisplay Pro';
            font-style: normal;
            font-stretch: normal;
            li {
                position: relative;
                margin-bottom: 26px;
                padding-left: 56px;
                list-style: none;
                &:last-child {
                    margin-bottom: 0;
                }
                b {
                    position: absolute;
                    left: 0;
                    top: -6px;
                    font-size: 80px;
                    font-weight: bold;
                    line-height: 70px;
                    letter-spacing: normal;
                    color: #c5f0bd;
                }
                p {
                    font-size: 18px;
                    font-weight: 900;
                    line-height: 1.28;
                    letter-spacing: 0.97px;
                    text-transform: uppercase;
                    margin-bottom: 4px;
                }
                span {
                    font-family: 'PF Din Text Cond Pro';
                    font-size: 15px;
                    font-weight: normal;
                    font-style: normal;
                    font-stretch: normal;
                    line-height: 1.27;
                    letter-spacing: 0.47px;
                    color: #002337;
                }
            }
        }
    }
}